<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 面包导航区结束 -->

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="success" icon="el-icon-circle-plus-outline" @click="addRoleDialog = true"
            >添加角色</el-button
          >
        </el-col>
      </el-row>
      <!-- 角色数据展示区域 -->
      <el-table
        :data="rolesList"
        stripe
        style="width: 100%"
        border
      >
        <!-- 索引效果 -->
        <!-- 扩展列渲染权限 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
              :class="['tag-bottom', i1 == 0 ? 'tag-top' : '', 'v-center']"
            >
              <!-- 一级权限渲染 每行一个标签 -->
              <el-col :span="6">
                <el-tag
                  closable
                  @close="deleteRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
              </el-col>
              <!-- 二级权限渲染 每行一个标签 -->
              <!-- 使用三级for循环进行嵌套 -->
              <el-col :span="18">
                <el-row
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                  :class="[i2 == 0 ? '' : 'tag-top', 'v-center']"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="deleteRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                  </el-col>
                  <!-- 三级权限渲染 每行多个标签-->
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      type="warning"
                      closable
                      @close="deleteRightById(scope.row, item3.id)"
                    >
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="roleName" label="角色名称"> </el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"
              >编辑</el-button
            >
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="showRightsTreeDialog(scope.row)"
              >分配权限</el-button
            >
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteRoleById(scope.row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 权限分配的树形弹框 -->
    <el-dialog title="权限分配" :visible.sync="rightsTreeDialog" width="50%" @close="resetRoleRights">
      <!-- 树形控件 -->
      <el-tree
        :data="rightsTreeList"
        :props="treeProps"
        show-checkbox
        default-expand-all
        :default-checked-keys="defaultKeys"
        node-key="id"
        ref="checkedRights"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button type="success" @click="assignRights">分配</el-button>
        <el-button type="info" @click="resetRoleRights">取消</el-button>
      </span>
    </el-dialog>
    <!-- 添加角色dialog -->
    <el-dialog title="添加角色" :visible.sync="addRoleDialog" width="60%" @close="resetAddRoleForm">
      <!-- 用户添加表单 -->
      <el-form ref="addRoleForm" :model="addRoleForm" :rules="addRoleFormRules" label-width="80px">
        <el-form-item label="角色名：" prop="roleName" label-width="auto">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述：" label-width="auto">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="success" @click="addRoleFormSubmit">添加</el-button>
        <el-button type="info" @click="resetAddRoleForm">取消</el-button>
      </span>
    </el-dialog>
    <!-- 修改角色dialog -->
    <el-dialog title="修改角色" :visible.sync="editRoleDialog" width="60%" @close="resetEditRoleForm">
      <!-- 用户添加表单 -->
      <el-form ref="editRoleForm" :model="editRoleForm" :rules="editRoleFormRules" label-width="80px">
        <el-form-item label="角色名：" prop="roleName" label-width="auto">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述：" label-width="auto">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="success" @click="editRoleFormSubmit">修改</el-button>
        <el-button type="info" @click="resetEditRoleForm">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 角色列表
      rolesList: [],
      // 权限分配的弹窗
      rightsTreeDialog: false,
      // 树形结构的权限
      rightsTreeList: [],
      // 配置
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // 获取指定角色默认选中的id
      defaultKeys: [],
      // 点击分配按钮当前的用户id值
      assignId: 0,
      // 添加角色的数据
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 添加角色的弹出框控制
      addRoleDialog: false,
      // 添加角色表单的验证规则
      addRoleFormRules: {
        roleName: [
          { required: true, message: '角色名必须填写', trigger: 'blur' },
          { min: 2, max: 20, message: '角色名长度应该在2-20个字符', trigger: 'blur' }
        ]
      },
      // 修改角色信息的数据
      editRoleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 添加角色的弹出框控制
      editRoleDialog: false,
      // 添加角色表单的验证规则
      editRoleFormRules: {
        roleName: [
          { required: true, message: '角色名必须填写', trigger: 'blur' },
          { min: 2, max: 20, message: '角色名长度应该在2-20个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    //   获取角色和权限数据
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.rolesList = res.data
    },
    // 根据权限标签id单独删除一个权限
    async deleteRightById(role, id) {
      // 先弹出弹窗是否确认删除
      const result = await this.$confirm(
        '此操作将永久删除该权限, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      console.log(result)
      if (result === 'cancel') {
        return null
      }
      // 确认了删除
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${id}`
      )
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)

      // 无需再重新加载所有权限
      // 只需要对现有的角色权限进行更新即可
      role.children = res.data
    },
    // 弹出窗口显示所有树形结构数据
    async showRightsTreeDialog(role) {
      // 保存当前的这一行点击的id
      this.assignId = role.id
      // 查询树形结构的弹窗数据
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.rightsTreeList = res.data
      // 把已存在的权限自动选上
      this.getThirdRights(role, this.defaultKeys)
      // 弹窗出现
      this.rightsTreeDialog = true
    },
    // 重置权限分配的弹窗内容
    resetRoleRights() {
      this.defaultKeys = []
      this.rightsTreeDialog = false
    },
    // 使用递归的方法实现查询第三级权限
    getThirdRights(role, arr) {
      // 如果这个权限不存在子节点，则说明这是三级权限
      if (!role.children) {
        return arr.push(role.id)
      }
      // 如果有children子节点则往下寻找
      role.children.forEach(val => {
        this.getThirdRights(val, arr)
      })
    },
    // 点击给当前角色分配权限
    async assignRights() {
      // 通过数组把权限拼接
      // 通过展开运算符拼接
      const keys = [
        ...this.$refs.checkedRights.getCheckedKeys(),
        ...this.$refs.checkedRights.getHalfCheckedKeys()
      ]
      // 拼接好的参数
      const idStr = keys.join(',')
      // 发送请求
      const { data: res } = await this.$http.post('roles/' + this.assignId + '/rights', { rids: idStr })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success('权限分配成功')
      // 重新获取最新的权限数据
      this.getRolesList()
      // 关闭弹出框
      this.rightsTreeDialog = false
    },
    // 重置添加角色表单
    resetAddRoleForm () {
      // 先隐藏
      this.addRoleDialog = false
      // 再清空表单
      this.$refs.addRoleForm.resetFields()
    },
    // 提交添加的数据
    addRoleFormSubmit() {
      // 先进行预验证
      this.$refs.addRoleForm.validate(async val => {
        if (!val) return this.$message.error(new Error('请填写正确的角色信息'))
        // 添加角色
        const { data: res } = await this.$http.post('roles', this.addRoleForm)
        if (res.meta.status !== 201) return this.$message.error(new Error(res.meta.msg))
        this.$message.success('添加' + this.addRoleForm.roleName + '角色成功')
        // 关闭弹出框
        this.addRoleDialog = false
        // 然后重新刷新页面数据
        this.getRolesList()
      })
    },
    // 根据id查询角色信息
    async getRoleInfoById (id) {
      // 查询信息
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.editRoleForm = res.data
    },
    // 显示editDialog
    showEditDialog(id) {
      // 先获取需要修改的数据
      this.getRoleInfoById(id)
      // 然后显示diaolog
      this.editRoleDialog = true
    },
    // 重置修改角色表单
    resetEditRoleForm () {
      // 先隐藏
      this.editRoleDialog = false
      // 再清空表单
      this.$refs.editRoleForm.resetFields()
    },
    // 提交修改的数据
    editRoleFormSubmit() {
      // 先进行预验证
      this.$refs.editRoleForm.validate(async val => {
        if (!val) return this.$message.error(new Error('请填写正确的角色信息'))
        // 修改角色信息
        const { data: res } = await this.$http.put('roles/' + this.editRoleForm.roleId, {
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc
        })
        if (res.meta.status !== 200) return this.$message.error(new Error(res.meta.msg))
        this.$message.success('修改' + this.editRoleForm.roleName + '角色成功')
        // 关闭弹出框
        this.editRoleDialog = false
        // 然后重新刷新页面数据
        this.getRolesList()
      })
    },
    // 删除角色通过id
    async deleteRoleById(id) {
      // 弹出确认框多次确认
      // 弹窗多次确认
      const result = await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (result === 'cancel') { return null }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 刷新页面数据
      this.getRolesList()
    }
  }
}
</script>
<style lang="less" scoped>
// tag标签上边距
.el-tag {
  margin: 10px;
}
// 上顶线
.tag-top {
  border-top: 1px solid #eee;
}
// 下边线
.tag-bottom {
  border-bottom: 1px solid #eee;
}
// 竖直居中
.v-center {
  display: flex;
  align-items: center;
}
</style>
